import React, { useState, useEffect, useRef, useContext, useMemo } from 'react';
import { Stack } from '@mui/system';
import { Grid } from '@mui/material';
import PROPERTY_TYPES from '../Component/PropertyTypes';
import axios from 'axios';
import { PropertyContext } from './Reducer/Context';
import SearchFilters from './SearchFilter';

import Chip from '@mui/material/Chip';





let PropertyTypes = ({ setIspropertyActive }) => {
    const [propertyType, setPropertyType] = useState(PROPERTY_TYPES)
    let { PropertyState, dispatch } = useContext(PropertyContext)
    const propertyWrapperRef = useRef(null)

    let handleClickOutside = (event) => {
        if (propertyWrapperRef && !propertyWrapperRef.current.contains(event.target)) {

            setIspropertyActive(false)
        }
    }
    useEffect(() => {
        document.addEventListener('mousedown', handleClickOutside);

        PropertyState.then(data => {
            let { propertyType } = data.filter
            setPropertyType(propertyType)
        })
            .catch(er => {
                console.log(er);
            })


        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        }


    }, [])

    useEffect(() => {
        dispatch({ type: "PropertyType", payload: propertyType })
    }, [propertyType])


    let option = propertyType.map(option => {
        let bgColor = option.color;
        let textColor = option.textColor
        return <div key={option.ID} className='PropertyFilters-Types'
            onClick={() => {
                let PT = propertyType.map((obj) => {
                    let { ID, checked } = obj

                    if (ID === option.ID) {
                        return { ...obj, checked: !checked };
                    } else {
                        return obj;
                    }
                })
                setPropertyType(PT)
            }}
        >
            <input type="checkbox" id='PropertyFilters-CheckBox'
                value={option.name}
                checked={option.checked}
            />
            <label style={{ backgroundColor: bgColor, color: textColor }} className='PropertyFilters-Label'>{option.name}</label>
        </div>
    })
    return (
        <div className='FilterContainer' ref={propertyWrapperRef}>
            <h3>Property type</h3>
            {option}
            <div className='PropertyType-Reset'>
                <button onClick={() => {
                    let PT = propertyType.map((obj) => { return { ...obj, checked: false } })
                    setPropertyType(PT)
                }}  >UnCheck all</button>
                <button onClick={() => {
                    let PT = propertyType.map((obj) => { return { ...obj, checked: true } })
                    setPropertyType(PT)
                }}   >Check all</button>

            </div>

        </div>
    )
}



let Areafilter = ({ arealabel, setIsAreaActive }) => {


    const [minArea, setMinArea] = useState("")
    const [maxArea, setMaxArea] = useState("")


    let { PropertyState, dispatch } = useContext(PropertyContext)

    const areaWrapperRef = useRef(null)

    let handleClickOutside = (event) => {
        if (areaWrapperRef && !areaWrapperRef.current.contains(event.target)) {
            console.log("Clicked outside");
            setIsAreaActive(false)
        }
    }


    let setLabel = (minArea, maxArea) => {
        let name;
        if (minArea > 0 && maxArea > 0 && minArea < maxArea) {
            name = `${minArea}m-${maxArea}m`
            dispatch({
                type: "Area", payload: {
                    min: minArea,
                    max: maxArea
                }
            })
        }
        else if (maxArea > 0) {
            name = `< ${maxArea}m`
            dispatch({
                type: "Area", payload: {
                    min: 0,
                    max: maxArea
                }
            })
        }
        else if (minArea > 0) {
            name = `> ${minArea}m`
            dispatch({
                type: "Area", payload: {
                    min: minArea,
                    max: 0
                }
            })
        }
        else {
            name = "Area"
        }

        return name
    }

    useEffect(() => {
        PropertyState.then(data => {
            let { area } = data.filter
            let { min, max } = area

            setMinArea(min)
            setMaxArea(max)

        })
            .catch(er => {
                console.log(er);
            })

    }, [])

    useEffect(() => {
        document.addEventListener('mousedown', handleClickOutside);



        arealabel(setLabel(minArea, maxArea))

        return () => {
            document.removeEventListener('mousedown', handleClickOutside);

        }
    }, [minArea, maxArea])


    return (<div className='FilterContainer' ref={areaWrapperRef} style={{
        border: 'none'
    }}>
        <p className="PlotAreaFilter-label"><b>Plot Area range (m<sup>2</sup>)</b></p>
        <Stack direction={'row'}>
            <input type="number" placeholder="min" id='areamin'
                name='min'
                value={minArea}
                onChange={(e) => {
                    setMinArea(parseInt(e.target.value));
                }}
            /> -
            <input type="number" placeholder="max" id='areamax'
                name='max' value={maxArea}
                onChange={(e) => {
                    setMaxArea(parseInt(e.target.value));
                }} />
        </Stack>
    </div>)
}



let GeographyFilters = ({ setButtonLabel, setIsGeographyActive }) => {


    const [districtList, setDistrictList] = useState([])
    const [mandalList, setMandalList] = useState([])
    const [villageList, setVillageList] = useState([])









    const geoWrapperRef = useRef(null)

    let handleClickOutside = (event) => {
        if (geoWrapperRef && !geoWrapperRef.current.contains(event.target)) {
            console.log("Clicked outside");
            setIsGeographyActive(false)
        }
    }


    useEffect(() => {
        document.addEventListener('mousedown', handleClickOutside);

        axios.get('Geo/Districts')
            .then(response => response.data
            )
            .then(data => {
                setDistrictList(data.NameList);
            })
            .catch(function (error) {
                // handle error
                console.log(error);
            })

        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        }

    }, [])
    const [selectedDistrict, setSelectedDistrict] = useState("undefined")
    const [selectedMandal, setSelectedMandal] = useState("undefined")
    const [selectedVillage, setSelectedVillage] = useState("undefined")

    let { PropertyState, dispatch } = useContext(PropertyContext)

    PropertyState.then(data => {
        let { district, mandal, village } = data.filter.geography
        let btnName = "Geography"
        if (Object.entries(district).length > 0 && district.level === 1) {
            let { District_ID, name } = district
            setSelectedDistrict(District_ID)


            if (Object.entries(mandal).length > 0 && mandal.level === 2) {
                let { Mandal_ID, name } = mandal
                setSelectedMandal(Mandal_ID)


                if (Object.entries(village).length > 0 && village.level === 3) {
                    let { Village_ID, name } = village
                    setSelectedVillage(Village_ID)

                }


            }
        }
    })
        .catch(er => {
            console.log(er);
        })

    useMemo(() => {
        if (selectedDistrict !== "undefined") {

            axios.get(`/geo/mandals?DistrictID=${selectedDistrict}`)
                .then(res => res.data)
                .then(data => {
                    setMandalList(data.NameList)
                    setSelectedMandal("undefined")
                })
        }
    }, [selectedDistrict])

    useMemo(() => {
        if (selectedMandal !== "undefined") {
            axios.get(`/geo/Villages?DistrictID=${selectedDistrict}&MandalID=${selectedMandal}`)
                .then(res => res.data)
                .then(data => {

                    setVillageList(data.NameList);
                    setSelectedVillage("undefined")
                })
        }
    }, [selectedMandal])


    return (<Stack className='FilterContainer' direction={'row'} spacing={2} ref={geoWrapperRef}>
        <div>
            <label htmlFor="district" id="GeographyFilters-Label">District</label>
            <select style={{
                width: '240px'
            }}
                value={selectedDistrict}
                onChange={(e) => {
                    let { id, name } = districtList.filter(ob => ob.id === parseInt(e.target.value))[0]
                    console.log(name);

                    dispatch({ type: "Geography-District", payload: { "District_ID": id, name, level: 1 } })
                }}
            >
                <option value={"undefined"} disabled>---Select---</option>
                {districtList.map(option => {
                    return <option key={option.id} value={option.id} data-name={option.name}>{option.name}</option>
                })}

            </select>
        </div>

        {selectedDistrict !== "undefined" && <div>
            <label htmlFor="district" id="GeographyFilters-Label">Mandal</label>
            <select style={{
                width: '180px'
            }}
                value={selectedMandal}
                onChange={(e) => {
                    setSelectedMandal(e.target.value)
                    let { id, name } = mandalList.filter(ob => ob.id === parseInt(e.target.value))[0]

                    dispatch({ type: "Geography-Mandal", payload: { "Mandal_ID": id, name, level: 2 } })
                }}
            >
                <option value={"undefined"} disabled>---Select---</option>
                {mandalList.map(option => {
                    return <option key={option.id} value={option.id}>{option.name}</option>
                })}
            </select>
        </div>}

        {selectedDistrict !== "undefined" && selectedMandal !== "undefined" && <div>
            <label htmlFor="district" id="GeographyFilters-Label">Village</label>
            <select
                className='GeoFilterSelect'
                value={selectedVillage} onChange={(e) => {
                    setSelectedVillage(e.target.value)
                    let { id, name } = villageList.filter(ob => ob.id === parseInt(e.target.value))[0]

                    dispatch({ type: "Geography-Village", payload: { "Village_ID": id, name, level: 3 } })
                }}  >
                <option value={"undefined"} disabled>---Select---</option>
                {villageList.map(option => {
                    return <option key={option.id} value={option.id}>{option.name}</option>
                })}
            </select>
        </div>}

    </Stack >)
}


function Filter({ idChipVisible, closeChip, properties }) {
    const [isGeographyActive, setIsGeographyActive] = useState(false)

    const [ispropertyActive, setIspropertyActive] = useState(false)
    const [isAreaActive, setIsAreaActive] = useState(false)


    const [geoButtonLabel, setgeoButtonLabel] = useState("Geography")
    const [areaLabel, setareaLabel] = useState("Area")

    const [searchTermLabel, setSearchTermLabel] = useState("")

    const searchRef = useRef()


    let selectedDistrict = "undefined"



    let { PropertyState, dispatch } = useContext(PropertyContext)

    PropertyState.then(data => {
        let { searchTerm, geography } = data.filter

        let { district, mandal, village } = geography

        let btnName = "Geography"
        if (Object.entries(district).length > 0 && district.level === 1) {
            let { District_ID, name } = district

            btnName = name + "- D"


            if (Object.entries(mandal).length > 0 && mandal.level === 2) {
                let { Mandal_ID, name } = mandal
                btnName = name + "- M"


                if (Object.entries(village).length > 0 && village.level === 3) {
                    let { Village_ID, name } = village
                    btnName = name + "- V"

                }
            }
        }
        if (btnName !== geoButtonLabel) {
            setgeoButtonLabel(btnName)
            setSearchTermLabel(searchTerm)

        }
      else  if(searchTerm !== searchTermLabel){
        setSearchTermLabel(searchTerm)

      }

    })
        .catch(er => {
            console.log(er);
        })


//     let getPropertyOwnerName = useMemo(() => {

//         PropertyState.then(({ properties }) => {

//             let traced_Name = properties.filter(obj => obj['ID'].toUpperCase() == searchTermLabel.toUpperCase())

//             let Owner_Name_Prop = properties.filter(obj => obj['Owner_Name'].toUpperCase() == traced_Name[0]['Owner_Name'].toUpperCase())
// console.log(Owner_Name_Prop,traced_Name);
//         })

//         return "Name"
//     }, [idChipVisible])

    return (<Grid container paddingBottom={1} style={{
        minHeight: '8vh',
        alignItems: 'center',
    }}>


        <Grid item xs={12} md={11}>

            <Stack direction={'row'} style={{
                flexWrap: 'wrap'
            }}>
                <SearchFilters ref={searchRef} />

                <div className='FilterWrapper' >
                    <button className="PropertyFilters-Button"
                        style={{
                            backgroundColor: isGeographyActive ? "#1963be" : "transparent",
                            color: isGeographyActive ? "white" : "black"
                        }}

                        onClick={() => {
                            setIsGeographyActive(!isGeographyActive)
                        }}

                    >{geoButtonLabel}</button>
                    {isGeographyActive && <GeographyFilters setButtonLabel={setgeoButtonLabel} setIsGeographyActive={setIsGeographyActive}
                        selectedDistrict={selectedDistrict}
                        dispatch={dispatch}
                    />}
                </div>


                <div className='FilterWrapper'>
                    <button className="PropertyFilters-Button"
                        style={{
                            backgroundColor: "#1963be",
                            color: "white"
                        }}

                        onClick={() => {
                            setIspropertyActive(!ispropertyActive)
                        }}
                    >Type</button>
                    {ispropertyActive && <PropertyTypes setIspropertyActive={setIspropertyActive} />}
                </div>


                <div className='FilterWrapper'>
                    <button className="PropertyFilters-Button" id='AreaFilters-Button'
                        style={{
                            backgroundColor: isAreaActive ? "#1963be" : "transparent",
                            color: isAreaActive ? "white" : "black"
                        }}

                        onClick={() => {
                            setIsAreaActive(!isAreaActive)
                        }}

                    >{areaLabel}</button>
                    {isAreaActive && <Areafilter arealabel={setareaLabel} setIsAreaActive={setIsAreaActive} />}

                </div>


                {idChipVisible && <div className='FilterWrapper' style={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    paddingLeft: '2.8em'
                }}>
                    <Chip label={searchTermLabel.toUpperCase()} variant="outlined" style={{
                        border: '2px solid #1963be',
                        color: 'black',
                        fontWeight: 'bold'
                    }} onDelete={() => {
                        if (searchRef?.current) {
                            searchRef.current.selectedTerm("")
                        }
                        closeChip()
                    }} />
                    {/* <Chip label={getPropertyOwnerName} /> */}
                </div>}
            </Stack>
        </Grid>


        <Grid item xs={12} md={1}>
            <div  >
                <button className="PropertyFilters-Button" id='AreaFilters-Button'
                    onClick={() => {
                        window.location.reload(false);
                    }}
                >Reset</button>
            </div>

        </Grid>

    </Grid>
    );
}

export default Filter;