import { Button, Grid, Typography, useMediaQuery } from '@mui/material';
import { Stack } from '@mui/system';
import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { isAuthenticated, removeToken } from './Auth';
import CustomTypography from './CustomTypography';

function Navbar(props) {

    const navigate = useNavigate()
    
    let mobileView = useMediaQuery('(max-width:900px)')
    return (<div  >
        <Grid container padding={1}
            paddingX={{ xs: 0, md: 2 }}
            alignItems={"center"} className="AppHeader"  >
            <Grid item xs={6} md={3}>
                <img src={'/airservelogo.png'} className='airserveLogo' alt="AirServe" />
            </Grid>

            {!mobileView && <Grid item md={6} >
                <Stack
                    flexDirection={'column'}
                    alignItems={'center'}
                >
                    <CustomTypography className="logoTop" content={'T-360'} />
                    <CustomTypography className="logoBottom" content={'Dharani Property Viewer'} />
                </Stack>
            </Grid>}


            <Grid item xs={6} md={3}>

                <Stack
                    flexDirection={'row'}
                    justifyContent={'flex-end'}
                >
                    {!isAuthenticated() ? <>
                        <Link to={'/signup'} style={{
                            textDecoration: 'none'
                        }}>
                            <button className='Auth-Button'>
                                Sign up
                            </button>
                        </Link>


                        <Link to={'/login'} style={{
                            textDecoration: 'none'
                        }}>
                            <button className='Auth-Button'>
                                Log In
                            </button>
                        </Link>
                    </>

                        : <button className='Auth-Button' onClick={() => {
                            removeToken()
                            navigate("/login")
                        }}>
                            Sign out
                        </button>}

                </Stack>
            </Grid>
        </Grid>
    </div>
    );
}

export default Navbar;