import { Grid } from '@mui/material';
import React, { useContext, useState, useEffect, useRef } from 'react';
import { Navigate } from 'react-router-dom';
import { isAuthenticated } from '../Component/Auth';
import Map from '../Component/Map';
import Navbar from '../Component/navbar';
import Sidebar from '../Component/Sidebar';
import Filter from './Filter';
import { PropertyContext } from './Reducer/Context';


let previousObj = {}

const typeOf = o => Object.prototype.toString.call(o);
const isObject = o => o !== null && !Array.isArray(o) && typeOf(o).split(" ")[1].slice(0, -1) === "Object";

const isPrimitive = o => {
    switch (typeof o) {
        case "object": {
            return false;
        }
        case "function": {
            return false;
        }
        default: {
            return true;
        }
    }
};


const getChanges = (previous, current) => {
    if (isPrimitive(previous) && isPrimitive(current)) {
        if (previous === current) {
            return "";
        }

        return current;
    }

    if (isObject(previous) && isObject(current)) {
        const diff = getChanges(Object.entries(previous), Object.entries(current));

        return diff.reduce((merged, [key, value]) => {
            return {
                ...merged,
                [key]: value
            }
        }, {});
    }

    const changes = [];

    if (JSON.stringify(previous) === JSON.stringify(current)) {
        return changes;
    }

    for (let i = 0; i < current.length; i++) {
        const item = current[i];

        if (JSON.stringify(item) !== JSON.stringify(previous[i])) {
            changes.push(item);
        }
    }

    return changes;
};



let Dashboard = ({setIdChipVisible}) => {
    let { PropertyState, dispatch } = useContext(PropertyContext)



    useEffect(() => {
        dispatch({ type: "SetInitialData" })

        return () => {
            dispatch({ type: "resetInintialdata" })
        }
    }, [])

    const [properties, setProperties] = useState([])
    const [geoJson, setGeoJson] = useState([])

    const mapRef = useRef()

    PropertyState.then(data => {

        PropertyState.then(data => {
            let { properties, geoJson } = data

            // console.log(  previousObj ,data,`Changes in ${Object.entries(getChanges(previousObj, data)).length }`);

            if (Object.entries(getChanges(previousObj, data)).length > 0) {
                setProperties(properties)
                setGeoJson(geoJson)
            }
            previousObj = data

        })
            .catch(er => {
                console.log(er);
            })

    })



    return (<Grid container >
        <Grid item md={7.5} xs={12} >
            <Map properties={properties} geoJson={geoJson} ref={mapRef} />
        </Grid>
        <Grid item md={4.5} xs={12} >
            <Sidebar properties={properties} openPopupOnMap={(property) => {

                let { ID,Owner_Name, GEOGRAPHY_ID } = property
                let { District_ID,
                    Mandal_ID,
                    Village_ID } = GEOGRAPHY_ID

                let geography = {
                    district: {  District_ID, name: "", level: 1 },
                    mandal: { Mandal_ID, name: "", level: 2 },
                    village: { Village_ID, name: "", level: 3 }
                }
                dispatch({ type: "openPopupOnMap", payload:{
                    searchTerm:ID.toLowerCase(),
                    geography
                } })
                setIdChipVisible(true)
                
            }} />
        </Grid>
    </Grid>)
}

function Home(props) {

    const [idChipVisible,setIdChipVisible]=useState(false)


    if (!isAuthenticated()) {
        return (<Navigate to='/login' />)
    }


    return (<div>
        <Navbar />

        <Filter idChipVisible={idChipVisible} closeChip={()=>{
            setIdChipVisible(false)
        }}   />
        <Dashboard setIdChipVisible={ setIdChipVisible } />
    </div>
    );
}

export default Home;