import { Button, Paper, Typography } from '@mui/material';
import { Stack } from '@mui/system';
import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import '../App.css' 
import axios from 'axios';
import CustomTypography from '../Component/CustomTypography';
import { setToken } from '../Component/Auth';
import Navbar from '../Component/navbar';
// import { setToken } from '../Component/Auth';

 

let FormGroup = ({ title, setValue, value, required, type = "text" }) => {
    return (<Stack direction={'column'} my={2}

        style={{
            marginTop: '1em'

        }}>
        <Stack direction={'row'}> 
            <CustomTypography className={"formLabel "} content={title} />
            <span className='req'>*</span>
        </Stack>
        <input type={type} value={value} className={'formInput'}
            placeholder={`Enter your ${title}`}

            onChange={(e) => {
                setValue(e.target.value);
            }} />
        {required && value.length == 0 &&
        <CustomTypography  className='FormErrMessage' content={`Please Enter the ${title}`} />
        }
    </Stack>)
}

function Login(props) {
    const [name, setName] = useState('')
    const [password, setPassword] = useState('')
    const [required, setRequired] = useState(false)


    const navigate = useNavigate()
    let submit = () => { 
        if (name !== "" && password !== "" ) {
           
            axios.post('users/login', {
                username: name, password

              }).then((res) => {
                let { token } = res.data
                setToken(token)
                navigate('/')
            }).catch(err => {
                console.log(err);
               alert("Username or password is  incorrect")
            })
        }
        else {
            setRequired(true)

        }
    }


    return (<div    >
        <Navbar />
 <video id='Airserve-Video' autoPlay muted loop  >
                    <source src='/homepage-video.mp4' type="video/mp4" />
                </video>
            <div sx={{
                width: '380px',
                padding: '2%',
                marginLeft: 'auto',

            }} className={'LogInForm'}>

          

<CustomTypography variant='h6' style={{
                    fontWeight: 'bold',
                color:"#dedede",
                fontSize:"1.4em"
                }} content={'Log In'} /> 
            <FormGroup title={'username'} value={name} setValue={setName} type="text" required={required} />
            <FormGroup title={'password'} value={password} setValue={setPassword} type="password" required={required} />
         
            <button 
                style={{
                    width: '90%',

                }}

                className='contained'
                onClick={submit}
            >Login</button>



            <Link to={'/signup'} style={{
                textDecoration: 'none'
            }}>
                
                <button  
                style={{
                    width: '90%',

                }}

                className='Auth-Button'>Create Account</button>
            </Link>

        </div>
    </div>
    );
}

export default Login;