



import React, { Component} from 'react';

import L from 'leaflet'
import { Stack, Box } from '@mui/system';
import '../App.css'
import axios from 'axios';
import CustomTypography from './CustomTypography';
import { PropertyContext } from '../Screen/Reducer/Context';
import { PropertyCount } from './PropertyCount';
import * as turf from '@turf/turf';

import PropertyTypes from './PropertyTypes';
import MapReport from './MapReport';
let mymap, osmUrl



let innerBoundaryLayer, OuterBoundaryLayer, PropertiesLayer, PropertiesLayerMarker = []


function maskName(name) {
    var regex = /\b(\w{2})(\w+)(\w)\b/g;
    return name.replace(
        regex,
        (_, first, middle, last) => `${first}${'*'.repeat(middle.length)}${last}`
    )
}

class Map extends Component {
    static contextType = PropertyContext
    constructor() {
        super()
        this.ref = React.createRef();
        this.state = {
            satelliteMap: true,
            boundaryLineVisible: true,
            plotLineVisible: true,
            violationVisible: false,
            bboxPropertyMapmove: false,
            propertyVisible: false,
            setReportData:[],
            isReportCardVisible:false

        }
    }




    addLayer = () => {
        axios.get("/data").then(res => {
            let { data: geojsonFeature } = res
            var geojson = L.geoJSON().addTo(mymap);
            geojson.addData(geojsonFeature);

            mymap.fitBounds(geojson.getBounds());
        })
    }



    componentDidMount() {
        mymap = L.map(this.ref.current).setView([17.8850, 78.459], 7);
        osmUrl = 'http://{s}.google.com/vt/lyrs=s,h&x={x}&y={y}&z={z}'
        var mapLayer = L.tileLayer(osmUrl, {
            subdomains: ['mt0', 'mt1', 'mt2', 'mt3']
        }).addTo(mymap);
        mymap.addLayer(mapLayer)


    }


    


    componentWillUnmount() {
        if (mymap !== undefined) { mymap.remove(); }
    }



    removeeachlayer = () => {
        mymap.eachLayer(function (layer) {
            if (osmUrl !== layer._url) mymap.removeLayer(layer);
        });
    };
    setSatelliteBase = () => {

        osmUrl = 'http://{s}.google.com/vt/lyrs=s,h&x={x}&y={y}&z={z}'
        var mapLayer = L.tileLayer(osmUrl, {
            subdomains: ['mt0', 'mt1', 'mt2', 'mt3']
        }).addTo(mymap);
        mymap.addLayer(mapLayer)
        this.setState({
            satelliteMap: true
        })
    }
    setStreetBase = () => {
        osmUrl = 'http://{s}.google.com/vt/lyrs=m&x={x}&y={y}&z={z}'
        var mapLayer = L.tileLayer(osmUrl, {
            subdomains: ['mt0', 'mt1', 'mt2', 'mt3']
        }).addTo(mymap);
        mymap.addLayer(mapLayer)
        this.setState({
            satelliteMap: false
        })
    }






    boundaryLine = () => {
        return (<>
            {this.state.boundaryLineVisible ?
                <CustomTypography variant={'body2'} content={"Hide Boundary Lines"} onClick={() => {
                    this.setState({
                        boundaryLineVisible: false
                    })

                }} className={"mapButton"} /> :
                <CustomTypography variant={'body2'} content={"Show Boundary Lines"}
                    onClick={() => {
                        this.setState({
                            boundaryLineVisible: true
                        })
                    }}
                    className={"mapButton"} />}
        </>)
    }


    plotLine = () => {
        return (<>
            {this.state.plotLineVisible ?
                <CustomTypography variant={'body2'} content={"Hide plot Lines"} onClick={() => {
                    this.setState({
                        plotLineVisible: false
                    })
                }} className={"mapButton"} /> :
                <CustomTypography variant={'body2'} content={"Show plot Lines"}
                    onClick={() => {
                        this.setState({
                            plotLineVisible: true
                        })
                    }}
                    className={"mapButton"} />}
        </>)
    }



    violation = () => {
        return (<>
            {!this.state.violationVisible ?
                <CustomTypography variant={'body2'} content={"Violation"}
                    onClick={() => {
                        this.setState({
                            violationVisible: true
                        }, () => {
                            this.context.dispatch({ type: "Violation", payload: true })
                        })

                    }}
                    className={"mapButton"}

                /> :
                <CustomTypography variant={'body2'} content={"All"}
                    onClick={() => {
                        this.setState({
                            violationVisible: false
                        }, () => {
                            this.context.dispatch({ type: "Violation", payload: false })
                        })
                    }}
                    className={"mapButton"} />}
        </>)
    }




    setInnerBoundary = (BoundaryJSON, properties, levelBoundary) => {
        let BoundaryGeoJsons = PropertyCount(BoundaryJSON, properties, levelBoundary)
        innerBoundaryLayer = L.geoJSON(BoundaryGeoJsons,
            {
                style: {
                    fillOpacity: 0,
                    fillColor: 'white',
                    color: '#3878d9',
                    weight: 2
                },
                onEachFeature: function (features, layer) {

                    let { name, X, Y, Count } = features.properties
                    let data = `<div>
            <h6>${name.toUpperCase()}</h6><h6>${Count} Properties</h6>
            </div>`
                    var icon = L.divIcon({
                        className: 'custom-div-icon',
                        html: `<div style='background-color:green;' class='Geo-marker-pin'></div>`,
                        iconAnchor: [0, 0]
                    });
                    var geojsonMarkerOptions = {
                        icon: icon
                    };
                    L.marker([Y, X], geojsonMarkerOptions).bindTooltip(data, { permanent: true, className: 'tooltip', direction: "center" }).addTo(mymap)
                }
            }).addTo(mymap)


        innerBoundaryLayer.on('click', (e) => {
            let attributes = e.layer.feature.properties
            let { Id, name } = attributes
            if (attributes.hasOwnProperty('Village_Zo')) {
                this.context.dispatch({ type: "Geography-Village", payload: { "Village_ID": Id, name, level: 3 } })

            }
            else if (attributes.hasOwnProperty('Mandal')) {
                this.context.dispatch({ type: "Geography-Mandal", payload: { "Mandal_ID": Id, name, level: 2 } })

            }
            else if (attributes.hasOwnProperty('District')) {
                this.context.dispatch({ type: "Geography-District", payload: { "District_ID": Id, name, level: 1 } })

            }
        })
    }


    setOuterBoundary(OuterBoundaryJSON) {
        let style = {
            fillOpacity: 0.2,
            fillColor: 'white',
            color: '#3878d9',
            weight: 2,
        };
        let OuterBoundaryGeoJsons = OuterBoundaryJSON.map(geo => { return geo.GEOJSON[0] })
        OuterBoundaryLayer = L.geoJSON(OuterBoundaryGeoJsons, { style: style }).addTo(mymap)

        mymap.fitBounds(OuterBoundaryLayer.getBounds())
    }

    setPropertiesLayer = (Properties) => {
        let FilterPropertiesjson = Properties.filter(geo => { return geo.GEOJSON })
        let PropertiesGeoJson = FilterPropertiesjson.map(prop => {
            prop.GEOJSON.properties = {
                ...prop.GEOJSON.properties,
                Owner_Name: maskName(prop.Owner_Name),
                PROPERTY_TYPE: prop.PROPERTY_TYPE,
                Plot_Area: prop.PLOT_AREA,
                Build_Up_Area: prop.BUILT_UP_AREA,
                Inspection: prop['Inspection_Indicator']
            }
            return prop.GEOJSON
        })

        PropertiesLayer = L.geoJSON(PropertiesGeoJson, {
            style: function (features) {
                let propColor;
                propColor = PropertyTypes.filter(type => {
                    return type.name === features.properties.PROPERTY_TYPE
                })
                return {
                    color: propColor[0].color,
                    weight: 2,
                }
            }, onEachFeature: (features, layer) => {

                let propColor;
                propColor = PropertyTypes.filter(type => { return type.name === features.properties.PROPERTY_TYPE })
                let MarkerColor = ''
                features.properties.Inspection === "V" ? MarkerColor = '#eb5065' : MarkerColor = propColor[0].color;
                var icon = L.divIcon({
                    className: 'custom-div-icon',
                    html: `<div style='background-color:${MarkerColor};' class='marker-pin'></div>`,
                    iconAnchor: [0, 0]
                });
                var geojsonMarkerOptions = { icon: icon };

                let indiv = features.properties
                var center = turf.centroid(features)

                let markers = L.marker([center.geometry.coordinates[1], center.geometry.coordinates[0]], geojsonMarkerOptions).addTo(mymap)

                PropertiesLayerMarker.push(markers)
                markers.bindPopup(`<div class="PropertyOverlayPopup">
            <div class="left">
            <img  src="/images/properties/${indiv.ID}.jpg" alt="" >
            </div>
            <div class="right">
                <b>${indiv.Owner_Name}</b>
                <p><b>Plot Area</b>: ${indiv.Plot_Area}</p>
                <p><b>Built-Up Area</b>: ${indiv.Build_Up_Area}</p>
            </div>
            </div> `);
                markers.on('mouseover', function (e) {
                    this.openPopup();
                });

                markers.on('mouseout', function (e) {
                    this.closePopup();
                });
                markers.on('click', (e) => {
                   this.setState({
                    isReportCardVisible:true,
                    setReportData:indiv
                   })
                });
            }
        }).addTo(mymap)

    }

    updated = () => {
        this.context.PropertyState.then(data => {
            this.removeeachlayer()

            let { Boundary: innerBoundary, OuterBoundary } = data.geoJson
            let levelBoundary = "District"
            let { district, mandal, village } = data.filter.geography

            // Set Properties and Zoom to it IF property === 1


            if (data.properties.length === 1) {
                this.setPropertiesLayer(data.properties)
            }
            else {
                if (Object.entries(district).length > 0 && district.level === 1) {
                    levelBoundary = "Mandal"
                    if (Object.entries(mandal).length > 0 && mandal.level === 2) {
                        levelBoundary = "Village"
                        if (Object.entries(village).length > 0 && village.level === 3) {
                            levelBoundary = "VillageView"
                            this.setPropertiesLayer(data.properties)
                        }

                    }
                }
            }


            if (OuterBoundary?.length > 0) this.setOuterBoundary(OuterBoundary)

            if (levelBoundary !== "VillageView" && innerBoundary?.length > 0) this.setInnerBoundary(innerBoundary, data.properties, levelBoundary)

            if (data.properties.length === 1) {
                mymap.fitBounds(PropertiesLayer.getBounds())

            }

        })

    }




    bbox = () => {
        var width = mymap.getBounds().getEast() - mymap.getBounds().getWest();
        var height = mymap.getBounds().getNorth() - mymap.getBounds().getSouth();

        console.log(
            'center:' + mymap.getCenter() + '\n' +
            'width:' + width + '\n' +
            'height:' + height + '\n' +
            'size in pixels:' + mymap.getSize()
        )

    }



    render() {
        return (<>
          <div style={{
                height: '100%',
                position: 'relative',
                minHeight: '500px'
            }}>
                <div ref={this.ref} style={{
                    width: '100%',
                    height: '100%'
                }} />



                <Box className="mapButtonWrapper">
                    <Stack direction={'row'} justifyContent={'space-between'} style={{
                        cursor: 'pointer',
                        flexWrap: 'wrap'
                    }}>
                        <Stack direction={'row'}>
                            <CustomTypography variant={'body2'} content={"Satellite"} className={"mapButton"}
                                style={{
                                    backgroundColor: !this.state.satelliteMap ? "rgb(196, 195, 195)" : "rgb(233, 231, 231)"
                                }}
                                onClick={this.setSatelliteBase} />
                            <CustomTypography variant={'body2'} content={"Street"} className={"mapButton"}
                                style={{
                                    backgroundColor: this.state.satelliteMap ? "rgb(196, 195, 195)" : "rgb(233, 231, 231)"
                                }}

                                onClick={this.setStreetBase} />




                        </Stack>
                        {/* {this.boundaryLine()} */}
                        {/* {this.plotLine()} */}
                        {this.violation()}


                    </Stack>
                </Box>

                {/* {this.state.bboxPropertyMapmove ? <Stack className="bboxPropertyMapmove" direction={"row"}
                    alignItems={'center'}
                    onClick={() => {
                        this.setState({
                            bboxPropertyMapmove: false
                        })
                        mymap.off('dragend', this.bbox)

                    }}>
                    <input type="checkbox" checked={true} />
                    <CustomTypography variant={'caption'} content={"Update results when map moves"}
                    />
                </Stack> : <Stack className="bboxPropertyMapmove" direction={"row"}
                    alignItems={'center'}
                    onClick={() => {
                        this.setState({
                            bboxPropertyMapmove: true
                        })
                        mymap.on('dragend', this.bbox)
                    }}>
                    <input type="checkbox" checked={false} />
                    <CustomTypography variant={'caption'} content={"Update results when map moves"}
                    />
                </Stack>} */}

                {this.updated()}
            </div>
{this.state.isReportCardVisible && <MapReport propertyDetail={this.state.setReportData} close={()=>{
    this.setState({
        isReportCardVisible:false,
        setReportData:{}
    })
}} />}
</>
          

        );
    }
}

export default Map;