import { Stack } from '@mui/system';
import React, { useEffect } from 'react';
import { useState } from 'react';
import CustomTypography from './CustomTypography';
let SetReport = ({ type, id }) => {


    const [reportImageExist,setReportImageExist]=useState(false)
    useEffect(()=>{

        let types = type.toLowerCase() + 'reports'
        let url = `/images/${types}/${id}.jpg`;
    
        const img = new Image();
        img.src = url;

        if (img.complete) {
            setReportImageExist(true);
        } else {
            img.onload = () => {
                setReportImageExist(true);
            };
            img.onerror = () => {
                setReportImageExist(false);
            };
        }


    },[id])
    

    return (<>
        {reportImageExist  ? <img id="reportImage" src={`/images/${type.toLowerCase() + 'reports'}/${id}.jpg`} style={{ width: '100%' }} alt='Report not available' /> :
            <div className="Report-Error-Message" id="reportError">
                <h3>{type} report not available</h3>
            </div>
        }</>)
}

function MapReport({propertyDetail,close }) {

    let {Build_Up_Area,
        ID,
        Inspection,
        Owner_Name,
        PROPERTY_TYPE,
        Plot_Area}=propertyDetail
    const [tabIdx, setTabIdx] = useState(0)



    
    return (<div className='ReportLightbox-wrapper'>
        <div className="ReportLightbox">
            <div className="ReportLightbox-header">
                {/* <span className="reportTitle" id='reportTitle'>{reportData[0]?.reportType} Report</span> */}
                <button className="reportCloser" style={{
                    marginLeft:'auto'
                }} onClick={close} >Close</button>

            </div>

            <Stack direction={"row"} style={{
                border:"2px solid #dedede"
            }}>
                <button className={tabIdx === 0 ? "map-Report-btn_active" : "map-Report-btn"} onClick={() => { setTabIdx(0) }}>
                    <div>Basic Details</div>
                </button>
                <button className={tabIdx === 1 ? "map-Report-btn_active" : "map-Report-btn"} onClick={() => { setTabIdx(1) }}>
                    <div>Inspection</div>
                </button>
                <button className={tabIdx === 2 ? "map-Report-btn_active" : "map-Report-btn"} onClick={() => { setTabIdx(2) }}>
                    <div>Tax</div>
                </button>

                <button className={tabIdx === 3 ? "map-Report-btn_active" : "map-Report-btn"} onClick={() => { setTabIdx(3) }}>
                    <div>Utility</div>
                </button>
                <button className={tabIdx === 4 ? "map-Report-btn_active" : "map-Report-btn"} onClick={() => { setTabIdx(4) }}>
                    <div>Registration</div>
                </button>
            </Stack>
            <div className="ReportLightbox-image" style={{
                backgroundColor:'#dedede'
            }}>
           {tabIdx ===0 &&  
                <Stack direction={'column'} spacing={2} paddingX={16}
                paddingY={8}>
                    <Stack direction={'row'} spacing={2} >
                        <b>
                            <CustomTypography content={"Id:"} />
                        </b>
                        <CustomTypography content={ID} />
                    </Stack>
                    <Stack direction={'row'} spacing={2}>
                        <b>
                            <CustomTypography content={"Owned by:"} />
                        </b>
                        <CustomTypography content={Owner_Name} className="PLC-owner" />
                    </Stack>
                    <Stack direction={'row'} spacing={2} >
                        <b>
                            <CustomTypography content={"Located at:"} />
                        </b>
                        <CustomTypography content={"maskName(truncateText(indiv.ADDRESS, 80))"} />
                    </Stack>


                    <Stack direction={'row'} spacing={2} >
                        <b>
                            <CustomTypography content={"Type:"} />
                        </b>
                        <CustomTypography content={PROPERTY_TYPE} />
                    </Stack>


                    <Stack direction={'row'}>
                        <b>
                            <CustomTypography content={"Plot Area:"} style={{
                                marginRight: '0.4em'
                            }} />
                        </b>
                        <CustomTypography content={Plot_Area} />
                    </Stack>
                    <Stack direction={'row'}>
                        <b>
                            <CustomTypography content={"Built-Up Area:"} style={{
                                marginRight: '0.4em'
                            }} />
                        </b>
                        <CustomTypography content={Build_Up_Area} />
                    </Stack>

                    

                    <Stack direction={'row'} spacing={1}  >
                    <b>{Inspection === 'C' ? 'Complaint' : 'Violations'}</b>
                    <span className="PLC-inspection"
                        style={{ background: Inspection === 'C' ? '#6cc648' : '#eb5065' }} />
                </Stack>
                </Stack>
           }


         
            {tabIdx ===1 && <SetReport  type={'Inspection'} id={ID}/>}
            {tabIdx ===2 && <SetReport  type={'Tax'} id={ID}/>}
            {tabIdx ===3 && <SetReport  type={'Utility'} id={ID}/>}
            {tabIdx ===4 && <SetReport  type={'Registration'} id={ID}/>}
            </div>
        </div>
    </div>

    );
}

export default MapReport;