import React, { useEffect, useReducer } from 'react';
import axios from 'axios';
import './App.css';
import Navbar from './Component/navbar';
import Home from './Screen/home';
import { inititalState, PropertyContext } from './Screen/Reducer/Context';
import { PropertyReducer } from './Screen/Reducer/Reducer';
import { Server } from './constant';

import { BrowserRouter, Routes, Route } from "react-router-dom";
import Login from './Screen/Login';
import Signup from './Screen/Signup';

axios.defaults.baseURL = Server


const Contact = () => {
  return <h1>Contact Me</h1>;
};



function App() {
  const [PropertyState, dispatch] = useReducer(PropertyReducer, inititalState())

 

  const PropertyValue = {
    PropertyState,
    dispatch
  }
  return (
    <PropertyContext.Provider value={PropertyValue}>

      <BrowserRouter>

        <Routes>
          <Route path={'/'} element={<Home />} />
          <Route path={'signup'} element={<Signup />} />
          <Route path={'login'} element={<Login />} />

        </Routes>
      </BrowserRouter>


    </PropertyContext.Provider>
  );
}

export default App;
