import { Button, Typography } from '@mui/material';
import {Stack } from '@mui/system';
import axios from 'axios';
import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import '../App.css'
import CustomTypography from '../Component/CustomTypography';
import Navbar from '../Component/navbar';

let FormGroup = ({ title, setValue, value, required, type = "text" }) => {
    return (<Stack direction={'column'} my={2}

        style={{
            marginTop: '1em'

        }}>
        <Stack direction={'row'}> 
            <CustomTypography className={"formLabel "} content={title} />
            <span className='req'>*</span>
        </Stack>
        <input type={type} value={value} className={'formInput'}
            placeholder={`Enter your ${title}`}

            onChange={(e) => {
                setValue(e.target.value);
            }} />
        {required && value.length == 0 &&
        <CustomTypography  className='FormErrMessage' content={`Please Enter the ${title}`} />
        }
    </Stack>)
}


const validateEmail = (mail) => {
    return /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()\.,;\s@\"]+\.{0,1})+([^<>()\.,;:\s@\"]{2,}|[\d\.]+))$/.test(mail);
};
function Signup(props) {
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [confirmPwd, setConfirmPwd] = useState('')
    const [required, setRequired] = useState(false)
    const navigate = useNavigate()


    let submit = () => {
        setRequired(true)

        if (!validateEmail(email)) {
            alert('Invalid Email')
            return
        }

        if (password.length === 0 || confirmPwd.length === 0) {
            alert("Password is Required")
            return
        }

        if (password !== confirmPwd) {
            alert("Mis matched password")
            return
        }



        if (validateEmail(email) && name !== '' && password === confirmPwd) {
 
            axios({
                method: 'post',
                url: '/users/createuser',
                data: {
                    username: name, email, password
                }
            }).then((res) => {
                navigate('/login')
            }).catch(err => {
                console.log(err);
            })
        }


    }


    return (
        <div   >
            <Navbar />
 <video id='Airserve-Video' autoPlay muted loop  >
                    <source src='/homepage-video.mp4' type="video/mp4" />
                </video>
            <div sx={{
                width: '380px',
                padding: '2%',
                marginLeft: 'auto',

            }} className={'SignUpForm'}>
               

                <CustomTypography variant='h6' style={{
                    fontWeight: 'bold',
                color:"#dedede",
                fontSize:"1.4em"
                }} content={'Create Account'} /> 
                <FormGroup title={'username'} type={'text'} value={name} setValue={setName} required={required} />
                <FormGroup title={'email'} type={'email'} value={email} setValue={setEmail} required={required} />
                <FormGroup title={'password'} type={'password'} value={password} setValue={setPassword} required={required} />
                <FormGroup title={'confirm password'} type={'text'} value={confirmPwd} setValue={setConfirmPwd} required={required} />

                <button className='contained'
                    style={{
                        width: '90%',
                    }}

                    onClick={submit}
                >Sign Up</button>


                <Stack direction={'row'}
                    marginTop={2}
                    alignItems={'center'}

                >


                    <Typography variant='body2' style={{
                        color:"#dedede"
                    }}>
                        Have already an account?
                    </Typography>
                    <Link to={'/login'}>
                        <Button style={{
                            color:"#1ab1e0"
                        }}>Login here</Button>
                    </Link>
                </Stack>
            </div>
        </div>
    );
}

export default Signup;