import axios from "axios";
import { createContext } from "react";
import PROPERTY_TYPES from "../../Component/PropertyTypes";
import { Server } from "../../constant";


export let inititalStateTemplate={
    filter: {
        searchTerm: "",
        propertyType: [...PROPERTY_TYPES],
        area: {
            min: "",
            max: ""
        },
        geography: {
            district: {},
            mandal: {},
            village: {}
        },
        Violation:false
    },
    properties: [],
    geoJson: []

}

 
export const  inititalState =async()=>{
    return { ...inititalStateTemplate} 
}
   
 

export const PropertyContext = createContext(inititalState)