import React, { Component } from 'react';
import { PropertyContext } from './Reducer/Context';

import '../App.css'

function maskName(name) {
    var regex = /\b(\w{2})(\w+)(\w)\b/g;
    return name.replace(
        regex,
        (_, first, middle, last) => `${first}${'*'.repeat(middle.length)}${last}`
    )
}


class SearchFilters extends Component {


    constructor() {
        super();
        this.searchWrapperRef = React.createRef();
        this.selectedTerm = this.selectedTerm.bind(this)

        this.state = {
            propertyList: [],
            updatedpropertyList: [],
            searchDiv: false,
            updatedSearchTerm: [],
            searchedTerm: ''
        }
    }

    static contextType = PropertyContext
    componentDidMount() {
        document.addEventListener('mousedown', this.handleClickOutside);

    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside);
    }

    handleClickOutside(event) {
        if (this.searchWrapperRef && !this.searchWrapperRef.current.contains(event.target)) {
            this.setState({ searchDiv: false })
        }
    }

    selectedTerm(val) {
        console.log(val);
        this.setState({ updatedSearchTerm: [], searchedTerm: val }, () => {
            this.context.dispatch({ type: "Search", payload: val })
        })
    }





    Searching = (e) => {
        e.preventDefault()

        let { value } = e.target
        let { PropertyState } = this.context

        PropertyState.then(({ properties }) => {

            let searchTerm = value.toLowerCase()

            let SearchedFilters = properties.map(obj => {
                let { Owner_Name, MAKAAN_ID, BHOOMI_ID } = obj
                Owner_Name = Owner_Name.toLowerCase()
                MAKAAN_ID = MAKAAN_ID.toLowerCase()
                BHOOMI_ID = BHOOMI_ID.toLowerCase()
                if (Owner_Name.includes(searchTerm) || MAKAAN_ID.includes(searchTerm) || BHOOMI_ID.includes(searchTerm)) {

                    if (MAKAAN_ID.includes(searchTerm)) {
                        return MAKAAN_ID
                    }
                    else if (BHOOMI_ID.includes(searchTerm)) {
                        return BHOOMI_ID
                    }
                    else {
                        return Owner_Name
                    }
                }
            })
            SearchedFilters = SearchedFilters.filter((obj) => obj !== undefined)
            let uniqueArray = Array.from(new Set(SearchedFilters));

            this.setState({
                searchedTerm: value,
                updatedSearchTerm: uniqueArray
            })
        })


    }



    render() {
        // this.context?.PropertyState.then(data => {
        //     let { searchTerm, geography } = data.filter
        //     console.log(searchTerm,this.state.searchedTerm);
        //     if (searchTerm !== this.state.searchedTerm && searchTerm !== "" ) {
        //         this.setState({
        //             updatedSearchTerm: [], searchedTerm: searchTerm
        //         })
        //     }
        
        // })
        return (<div className='FilterWrapper' >
            <form className='SearchForm'>
                <input type='text' value={this.state.searchedTerm}
                    placeholder='Search' autoComplete="off" onChange={this.Searching}
                    className="deletable"
                />
                <span className='deletableIcon' onClick={() => {this.selectedTerm("")


                }}>&#x2715;</span>
            </form>

            {this.state.updatedSearchTerm.length > 0 && <div className='searched-NameList FilterContainer'>
                {this.state.updatedSearchTerm.map((names, index) => {

                    const term = names

                    const matchIdx = names.toLowerCase().indexOf(this.state.searchedTerm.toLowerCase());
                    return (<div key={index} id='searchList' className='Property-SearchingList'
                        onClick={() => { this.selectedTerm(names) }}
                    >
                        {term.substr(0, matchIdx)}<strong>{term.substr(matchIdx, this.state.searchedTerm.length)}</strong>
                        {term.substr(matchIdx + this.state.searchedTerm.length)}</div>)
                })}
            </div>
            }

        </div>)


    }

}

export default SearchFilters;