export function PropertyCount(Geo,Properties,LevelBoundary="District"){
 LevelBoundary= LevelBoundary+ '_ID'
          let propertyGeographyId =Properties.map(prop => { return prop.GEOGRAPHY_ID[LevelBoundary] })
        propertyGeographyId = [...new Set(propertyGeographyId)]
 
  // /Extracting geo which has Property
        Geo= propertyGeographyId.map(id => {
         let Contained_Geo =Geo.filter(geo => { return geo.id === id })
             return Contained_Geo[0] }) 
 



  let Counted=  Geo.map(geo=>{
      let TotalCount=  Properties.filter(prop=>{ 
        return prop.GEOGRAPHY_ID[LevelBoundary] === geo.id}).length
 
    
    let {Area,Length,Name,...Geojson}=geo.GEOJSON[0].properties
    Geojson={...Geojson,name:geo.name,Count:TotalCount,Id:geo.id}
              geo.GEOJSON[0].properties=Geojson
      return geo
    })


  let CountedGeo=  Counted=Counted.map(geo=>{return geo.GEOJSON[0]})
  return CountedGeo
}