import { Typography } from '@mui/material';
import { Stack } from '@mui/system';
import React, { useEffect, useState, useMemo, useRef, } from 'react';
import CustomTypography from './CustomTypography';
import Pagination from './Pagination';
let PageSize = 50

function truncateText(text, length) {
    if (text.length <= length) {
        return text;
    }

    return text.substr(0, length) + '\u2026'
}


//   function setReport( ID,reportType){
//     return {
//         type: 'Reports',ID,reportType
//     }
// }



function maskName(name) {
    var regex = /\b(\w{2})(\w+)(\w)\b/g;
    return name.replace(
        regex,
        (_, first, middle, last) => `${first}${'*'.repeat(middle.length)}${last}`
    )
}
let PropertyCard = ({ indiv, setReport, openPopupOnMap }) => {
    let id = indiv['MAKAAN_ID'];
    if (id === 'N/A') {
        id = indiv['BHOOMI_ID'];
    }
    return (<div className="PlaceListingCard" id={id} key={id} >
        <div className="PLC-image"  >
            <img src={`/images/properties/${id}.JPG`} alt="" />

            <div className="PLC-top-left PLC-prop-id">
                <strong>ID</strong>: {id}
            </div>
            <div className="PLC-prop-summary">
                <p>{indiv.PROPERTY_TYPE}</p>
            </div>
        </div>
        <div>
            <Stack direction={'row'} justifyContent={'space-between'}>
                <Stack direction={'row'}>
                    <b>
                        <CustomTypography content={"Plot Area:"} style={{
                            marginRight: '0.4em'
                        }} />
                    </b>
                    <CustomTypography content={indiv.PLOT_AREA} />
                </Stack>
                <Stack direction={'row'}>
                    <b>
                        <CustomTypography content={"Built-Up Area:"} style={{
                            marginRight: '0.4em'
                        }} />
                    </b>
                    <CustomTypography content={indiv.BUILT_UP_AREA} />
                </Stack>
            </Stack>


            <Stack direction={'column'} spacing={'0.8%'}>
                <Stack direction={'column'} spacing={-0.8}>
                    <b>
                        <CustomTypography content={"Owned by:"} />
                    </b>
                    <CustomTypography content={maskName(truncateText(indiv.Owner_Name, 64))} className="PLC-owner" />
                </Stack>
                <Stack direction={'column'} spacing={-0.8} >
                    <b>
                        <CustomTypography content={"Located at:"} />
                    </b>
                    <CustomTypography content={maskName(truncateText(indiv.ADDRESS, 80))} />
                </Stack>

                <Stack direction={'row'} spacing={1}  >
                    <b>{indiv['Inspection_Indicator'] === 'C' ? 'Complaint' : 'Violations'}</b>
                    <span className="PLC-inspection"
                        style={{ background: indiv['Inspection_Indicator'] === 'C' ? '#6cc648' : '#eb5065' }} />
                </Stack>
            </Stack>
        </div>
        <div className="PLC-detail PLC-report-detail">
            <Stack direction={'row'} justifyContent={'space-between'} paddingX={1} >
                <p><b>Reports</b></p>

                <div onClick={() => { 
                    openPopupOnMap(indiv)
                }}>
                    <img src={`/map.png`} alt={'Location'} className={'mapTriggerIcon'} />

                </div>
            </Stack>


            <Stack direction={'row'}  >
                <button className="PLC-button" onClick={() => { setReport(id, 'Inspection') }}>
                    <div>Inspection</div>
                </button>
                <button className="PLC-button" onClick={() => { setReport(id, 'Tax') }}>
                    <div>Tax</div>
                </button>
            </Stack>

            <Stack direction={'row'}  >
                <button className="PLC-button" onClick={() => { setReport(id, 'Utility') }}>
                    <div>Utility</div>
                </button>
                <button className="PLC-button" onClick={() => { setReport(id, 'Registration') }}>
                    <div>Registration</div>
                </button>
            </Stack>
        </div>
    </div>
    )
}
function Sidebar({ properties,openPopupOnMap }) {

    const sidebarRef = useRef(null)
    const [currentPage, setCurrentPage] = useState(1);

    const [reportCard, setReportCard] = useState(false)
    const [reportData, setReportData] = useState([])



    useEffect(() => {
        setCurrentPage(1)
        if (sidebarRef.current) {
            sidebarRef.current.scrollTo({
                top: 0,
                behavior: 'smooth',
            })
        }

    }, [properties])


    let setReport = (id, type) => {

        setReportCard(true)
        let types = type.toLowerCase() + 'reports'
        let url = `/images/${types}/${id}.jpg`;
        checkIfImageExists(url, (exists) => {
            if (exists) {
                setReportData([{
                    id: id,
                    reportType: type,
                    reportImageExist: true,
                    reportImageUrl: url
                }])
            }
            else {
                setReportData([{
                    id: id,
                    reportType: type,
                    reportImageExist: false,
                    reportImageUrl: ''
                }])
            }
        });
    }


    let checkIfImageExists = (url, callback) => {
        const img = new Image();
        img.src = url;

        if (img.complete) {
            callback(true);
        } else {
            img.onload = () => {
                callback(true, url);
            };
            img.onerror = () => {
                callback(false, url);
            };
        }
    }

    const currentTableData = useMemo(() => {
        const firstPageIndex = (currentPage - 1) * PageSize;
        const lastPageIndex = firstPageIndex + PageSize;
        // console.log(firstPageIndex, lastPageIndex);
        return properties.slice(firstPageIndex, lastPageIndex);
    }, [currentPage, properties]);



    if (properties.length === 0) return (
        <div className='PropertyContainer' style={{
            justifyContent: 'center',
            alignContent: 'center'
        }} >
            <Typography variant='h5'>No result information.......</Typography >
            <Typography variant='subtitle1'>
                We couldnt find any data matching your search/filter
            </Typography>
        </div>)

    else if (properties.length >= PageSize) {
        {
            properties.map((data, index) => {
                return (<PropertyCard indiv={data} key={index} setReport={setReport} openPopupOnMap={openPopupOnMap} />)
            })
        }

    }
    return (<>


        <div className='PropertyContainer' ref={sidebarRef}>
            <div className="PropertyContainer-Heading" >
                <h3 id='totalprop'>{properties.length} Properties</h3>
            </div>

            {currentTableData.map((data, index) => {
                return (<PropertyCard indiv={data} key={index} setReport={setReport} openPopupOnMap={openPopupOnMap} />)
            })}
            <Pagination
                className="pagination-bar"
                currentPage={currentPage}
                totalCount={properties.length}
                pageSize={PageSize}
                onPageChange={page => {
                    setCurrentPage(page)
                    sidebarRef.current.scrollTo({
                        top: 0,
                        behavior: 'smooth',
                    })
                }}
            />
        </div>
        {reportCard && reportData.length > 0 && <div className='ReportLightbox-wrapper'>
            <div className="ReportLightbox">
                <div className="ReportLightbox-header">
                    <span className="reportTitle" id='reportTitle'>{reportData[0].reportType} Report</span>
                    <button className="reportCloser" onClick={() => {
                        setReportCard(false)
                        setReportData([])
                    }} >Close</button>

                </div>
                <div className="ReportLightbox-image">
                    {reportData[0].reportImageExist === true ? <img id="reportImage" src={reportData[0].reportImageUrl} style={{ width: '100%' }} alt='Report not available' /> :
                        <div className="Report-Error-Message" id="reportError">
                            <h3>{reportData[0].reportType} report not available</h3>
                        </div>
                    }
                </div>
            </div>
        </div>}

    </>

    );
}
export default Sidebar;