function getToken() {
    console.log("Starting to check");
   return  !!localStorage.getItem('tbirds-AS-Token')
  }

  function setToken(token) {
    localStorage.setItem('tbirds-AS-Token', JSON.stringify(token));
  }


  function removeToken() {
    localStorage.removeItem('tbirds-AS-Token');
   

  }

  function isAuthenticated(){
     return  getToken()
  }
 
  export {isAuthenticated,setToken,removeToken};


  
  