import axios from "axios";
import { Server } from "../../constant";
import { inititalState, inititalStateTemplate } from "./Context"

let GeoFiles = []
let PropertyFiles = []

let RedefinedFilters = { ...inititalStateTemplate.filter }

export async function getApiData(GeoURL, PropertyURL, callBack) {
    let GeoData = []; let PropertyData = []
    let assigntoGeoData = (data) => { GeoData = data }
    let assigntoProperty = (data) => { PropertyData = data }
    await axios.get(GeoURL).then(response => {
        assigntoGeoData(response.data)
        console.log(response.data);
    }).catch(err => {
        console.log(err.message);
    })
    await axios.get(`${Server}/${PropertyURL}`).then(response => {
        assigntoProperty(response.data)
    }).catch(error => {
        alert(error.response.data.message)
    })
    if (Object.keys(GeoData).length > 0) callBack(GeoData, PropertyData)
}






async function geoData(GeoURL, callBack) {
    let GeoData = []
    let assigntoGeoData = (data) => { GeoData = data }
    await axios.get(GeoURL).then(response => {
        assigntoGeoData(response.data)
        console.log(response.data);
    }).catch(err => {
        console.log(err.message);
    })

    if (Object.keys(GeoData).length > 0) callBack(GeoData)
}



let SearchFilter = (properties, searchTerm) => {
    return properties.filter(obj => {
        let { Owner_Name, MAKAAN_ID, BHOOMI_ID } = obj
        Owner_Name = Owner_Name.toLowerCase()
        MAKAAN_ID = MAKAAN_ID.toLowerCase()
        BHOOMI_ID = BHOOMI_ID.toLowerCase()
        return Owner_Name.includes(searchTerm) || MAKAAN_ID.includes(searchTerm) || BHOOMI_ID.includes(searchTerm)
    })
}


let PropertyTypeFilter = (properties, payload) => {
    let newArray = []
    properties.forEach(prop => {
        payload.forEach(({ checked, name }) => {
            if (checked && prop['PROPERTY_TYPE'] === name) {
                newArray.push(prop)
            }
        })
    })
    return newArray
}




let AreaFilter = (properties, payload) => {

    let { min, max } = payload
    if (min > 0 && max > 0) {
        return properties.filter(feature => {
            let u = parseInt(feature.PLOT_AREA.replace(/[,]/g, ''));

            return ((u > min) && (u < max))
        })
    }
    else if (max > 0) {
        return properties.filter(feature => {
            return (parseInt(feature.PLOT_AREA.replace(/[,]/g, '')) < max)
        })
    }
    else if (min > 0) {
        return properties.filter(feature => {
            return (parseInt(feature.PLOT_AREA.replace(/[,]/g, '')) > min)
        })

    }

    else {
        return properties
    }
}





let GeographyFilter = (properties, level = 0, payload = {}) => {
    if (level === 1) {
        return properties.filter(obj => {
            return obj['GEOGRAPHY_ID']['District_ID'] === payload['District_ID']
        })
    }

    else if (level === 2) {
        return properties.filter(obj => {
            return obj['GEOGRAPHY_ID']['Mandal_ID'] === payload['Mandal_ID']
        })
    }

    else if (level === 3) {
        return properties.filter(obj => {
            return obj['GEOGRAPHY_ID']['Village_ID'] === payload['Village_ID']
        })
    }

    else {
        return properties
    }

}



let drillDown = (filterObj) => {
    // PropertyFiles 
    RedefinedFilters = filterObj

    let {
        propertyType, searchTerm, area, geography, Violation
    } = filterObj



    let updatedProperties = PropertyTypeFilter(PropertyFiles, propertyType)

    let { district, mandal, village } = geography
    if (Object.entries(district).length > 0 && district.level === 1) {
        updatedProperties = GeographyFilter(updatedProperties, 1, district)


        if (Object.entries(mandal).length > 0 && mandal.level === 2) {
            updatedProperties = GeographyFilter(updatedProperties, 2, mandal)

            if (Object.entries(village).length > 0 && village.level === 3) {
                updatedProperties = GeographyFilter(updatedProperties, 3, village)
            }


        }

    }

    console.log(updatedProperties);
    updatedProperties = SearchFilter(updatedProperties, searchTerm)
    updatedProperties = AreaFilter(updatedProperties, area)


    if (Violation) {
        updatedProperties = updatedProperties.filter(obj => {
            return obj['Inspection_Indicator'] === "V"
        })
    }


    return updatedProperties
}

export const PropertyReducer = async (state, action) => {
    let { type, payload } = action
    switch (type) {
        case "SetInitialData":
            await getApiData(`/Geo/Districts`,
                `Properties`, (GeoData, PropertyData) => {
                    GeoFiles = GeoData.GeoJsons;
                    PropertyFiles = PropertyData;
                })

            return {
                ...inititalStateTemplate, properties: PropertyFiles,
                geoJson: GeoFiles
            }

        case "Area":
            let AreaFiltersObj = {
                ...RedefinedFilters, area: payload
            }
            return {
                properties: drillDown(AreaFiltersObj),
                filter: { ...AreaFiltersObj },
                geoJson: GeoFiles

            }


        case "Geography-District":
            let GeographyFilterDistrictsObj = {
                ...RedefinedFilters, geography: {
                    district: payload,
                    mandal: {},
                    village: {}
                }
            }

            await geoData(`/geo/mandals?DistrictID=${payload.District_ID}`, (GeoData) => {
                GeoFiles = GeoData.GeoJsons;
            })


            return {
                properties: drillDown(GeographyFilterDistrictsObj),
                filter: { ...GeographyFilterDistrictsObj },
                geoJson: GeoFiles
            }


        case "Geography-Mandal":
            let GeographyFilterMandalsObj = {
                ...RedefinedFilters, geography: {
                    ...RedefinedFilters.geography,
                    mandal: payload,
                    village: {}
                }
            }

            await geoData(`/geo/Villages?DistrictID=${RedefinedFilters.geography.district.District_ID}&MandalID=${payload.Mandal_ID}`, (GeoData) => {
                GeoFiles = GeoData.GeoJsons;
            })

            return {
                properties: drillDown(GeographyFilterMandalsObj),
                filter: { ...GeographyFilterMandalsObj },
                geoJson: GeoFiles
            }

        case "Geography-Village":
            let GeographyFilterVillageObj = {
                ...RedefinedFilters, geography: {
                    ...RedefinedFilters.geography,
                    village: payload
                }
            }

            await geoData(`/geo//Villages/Village?DistrictID=${RedefinedFilters.geography.district.District_ID}&MandalID=${RedefinedFilters.geography.mandal.Mandal_ID}&VillageID=${payload.Village_ID}`, (GeoData) => {
                GeoFiles = GeoData.GeoJsons;
                console.log(GeoFiles);
            })


            return {
                properties: drillDown(GeographyFilterVillageObj),
                filter: { ...GeographyFilterVillageObj },
                geoJson: GeoFiles

            }


        case "Search":
            let SearchFiltersObj = {
                ...RedefinedFilters, searchTerm: payload
            }
            return {
                properties: drillDown(SearchFiltersObj),
                filter: { ...SearchFiltersObj },
                geoJson: GeoFiles

            }

        case "PropertyType":
            let PropertyFiltersObj = {
                ...RedefinedFilters, propertyType: payload
            }
            return {
                properties: drillDown(PropertyFiltersObj),
                filter: { ...PropertyFiltersObj },
                geoJson: GeoFiles
            }

        case "Violation":
            let ViolationFiltersObj = {
                ...RedefinedFilters, Violation: payload
            }
            return {
                properties: drillDown(ViolationFiltersObj),
                filter: { ...ViolationFiltersObj },
                geoJson: GeoFiles
            }

        case "openPopupOnMap":
            let openPopupOnMapFilterObj = {
                ...RedefinedFilters, ...payload
            }
          
            await geoData(`/geo//Villages/Village?DistrictID=${payload.geography.district.District_ID}&MandalID=${payload.geography.mandal.Mandal_ID}&VillageID=${payload.geography.village.Village_ID}`, (GeoData) => {
                GeoFiles = GeoData.GeoJsons;
                if (GeoFiles?.OuterBoundary[0]?.name) {
                    openPopupOnMapFilterObj.geography.village.name = GeoFiles?.OuterBoundary[0]?.name
                }
            })

            return {
                properties: drillDown(openPopupOnMapFilterObj),
                filter: { ...openPopupOnMapFilterObj },
                geoJson: GeoFiles
            }


        default:
            await getApiData(`Geo/State`,
                `Properties`, (GeoData, PropertyData) => {
                    GeoFiles = GeoData; PropertyFiles = PropertyData;
                })

            return {
                ...inititalStateTemplate, properties: PropertyFiles,
                geoJson: GeoFiles
            }
    }
}

